/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react'
import creditCardImg from '../../assets/head/credit_card.png'
import activityImg from '../../assets/head/activity.png'
import haagenDazsImg from '../../assets/head/haagen_dazs.png'

const HeadContentCreditCardStyle = css`
    display: flex;
    align-items: center;

    background-color: #495b83;
    border: 3px solid #35536b;
    border-radius: 15px;
    padding: 5px 10px;
    @media (max-width: 768px) {
        /* move credit component to below */
        position: absolute;
        top: 100%;
        width: 100%;
        box-sizing: border-box;
        border-radius: 0;
        justify-content: center;
    }
`

const itemStyle = css`
    display: flex;
`

const itemTextStyle = css`
    /* border: 1px solid blue; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    p:first-of-type {
        color: white;
        font-family: Segoe UI;
        font-weight: 600;
    }
    p:last-child {
        color: #fcc8d1;
    }
`

function HeadContentCreditCard() {
    return (
        <div css={HeadContentCreditCardStyle}>
            {/* item one */}
            <div css={itemStyle}>
                <div css={itemTextStyle}>
                    <p>React 卡</p>
                    <p>5% 無上限</p>
                </div>
                <div>
                    <img
                        src={creditCardImg}
                        css={css`
                            display: block;
                            width: 60px;
                            height: auto;
                        `}
                        alt=""
                    />
                </div>
            </div>

            {/* item two */}
            <div>
                <div>
                    <img
                        src={activityImg}
                        css={css`
                            display: block;
                            margin: 0 -15px;
                            width: 100px;
                            height: auto;
                        `}
                        alt=""
                    />
                </div>
            </div>

            {/* item three */}
            <div css={itemStyle}>
                <div css={itemTextStyle}>
                    <p>滿額抽</p>
                    <p>哈根達斯</p>
                </div>
                <div>
                    <img
                        src={haagenDazsImg}
                        css={css`
                            display: block;
                            width: 60px;
                            height: auto;
                        `}
                        alt=""
                    />
                </div>
            </div>
        </div>
    )
}

export default HeadContentCreditCard
