/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react'

import HeadContentLogo from './HeadContentLogo.jsx'
import HeadContentSearch from './HeadContentSearch.jsx'
import HeadContentCreditCard from './HeadContentCreditCard.jsx'

function TheHeadContent() {
    return (
        <div
            css={css`
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-top: 60px;
                padding: 50px 10%;
                background-color: #282c34;
                @media (max-width: 1200px) {
                    padding: 50px 5%;
                }
                @media (max-width: 992px) {
                    padding: 50px 6%;
                }
                @media (max-width: 768px) {
                    flex-direction: column;
                    padding: 25px 6%;

                    /* move credit component to below */
                    position: relative;
                }
                @media (max-width: 576px) {
                    padding: 25px 1%;

                    /* move credit component to below */
                    position: relative;
                }
            `}
        >
            <div
                css={css`
                    display: flex;
                    align-items: center;

                    flex: 1 1 auto;
                    @media (max-width: 992px) {
                        flex-direction: column;
                        flex: 0 1 auto;
                    }
                `}
            >
                <HeadContentLogo />
                <HeadContentSearch />
            </div>
            <HeadContentCreditCard />
        </div>
    )
}

export default TheHeadContent
