const styleMenu = css`
    /* border: 1px solid red; */
    
`

function HomeMenu() {
    return (
        <div css={styleMenu}>
            <div css={css`
                background-color: #515151;
                /* border: 1px solid red; */
                position: relative;
            `}>   
                {/* Menu Title */}
                <div css={css`
                    padding: 5px 14px;
                    font-weight: 500;
                    background-color: #515151;
                    color: white;
                    box-shadow: -3px 20px 15px -8px #050505b5;

                    position: absolute;
                    width: 100%;
                    box-sizing: border-box;
                `}>
                    <p>全站商品分類</p>
                </div>

                {/* Menu List */}
                <ul css={css`
                    padding: 40px 0px 15px;
                    li { 
                        margin: 8px 0;
                        a { 
                            display: block;
                            color: #ffffff;
                            padding: 5px 13px;
                            &:hover {
                                background-color: #61dafb;
                            }
                        }

                    }
                    
                `}>
                    <li>
                        <a href="">
                            家電・電玩・視聽・廚三機
                        </a>
                    </li>
                    <li>
                        <a href="">
                            手機・平板・耳機・攝相機
                        </a>
                    </li>
                    <li>
                        <a href="">
                            電腦・電競・週邊・零組件
                        </a>
                    </li>
                </ul>
            </div>
        </div>
    )
}

export default HomeMenu