/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react'

// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react'
import { Autoplay } from 'swiper'
// Import Swiper styles
import 'swiper/css'
import 'swiper/css/pagination'
import 'swiper/css/navigation'

// must be here, it need be loaded last
import './Banner.scss'

// import required modules
import { Navigation, Pagination } from 'swiper'

// import icon
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos'

const SwiperStyle = css`
    width: 800px;
    /* border: 1px solid red; */
`

function Banner() {
    const pagination = {
        clickable: true,
        // clickableClass: 'x',
        bulletClass: 'dot',
        bulletActiveClass: 'dot-active',
        renderBullet: function (_, className) {
            return `<div class="${className}"><div class="dot-color"></div></div>`
        },
    }

    const navigation = {
        prevEl: '.button-prev',
        nextEl: '.button-next',
    }

    const autoplay = {
        // mouse 進入 banner autoplay 暫停， mouse 離開後 autoplay 開始
        disableOnInteraction: false, // document 寫到，此 property 若為 true， mouse 進入會是停止 autoplay，而不是暫停 autoplay
        pauseOnMouseEnter: true, // mouse 進入 banner autoplay 暫停
    }
    return (
        // <Swiper pagination={true} modules={[Pagination]} css={SwiperStyle}>
        <Swiper
            pagination={pagination}
            navigation={navigation}
            autoplay={autoplay}
            loop={true}
            modules={[Pagination, Navigation, Autoplay]}
            css={SwiperStyle}
        >
            {[1, 2, 3, 4, 5, 6, 7].map((n) => {
                return (
                    <SwiperSlide key={n}>
                        <img
                            className="slide-img"
                            src={require(`../../assets/banner/banner_0${n}.png`)}
                            alt=""
                        />
                    </SwiperSlide>
                )
            })}
            <div className="button-prev">
                <ArrowBackIosNewIcon
                    sx={{ fontSize: 40, '&:hover': { color: '#38566f' } }}
                />
            </div>
            <div className="button-next">
                <ArrowForwardIosIcon sx={{ fontSize: 40, '&:hover': { color: '#38566f' } }} />
            </div>
        </Swiper>
    )
}

export default Banner
