/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react'
import classes from './TheHeader.module.scss'

import { Link } from 'react-router-dom'
import { useState } from 'react'

// import styled from 'styled-components'
import HeaderHambuger from './HeaderHambuger.jsx'

// import HeaderMenu from './HeaderMenu.jsx'
import HeaderLink from './HeaderLink.jsx'
import HeaderMemberLink from './HeaderMemberLink.jsx'

function TheHeader() {
    const [show, setShow] = useState(false)

    return (
        <div
            css={css`
                display: flex;
                height: 60px;
                /* max-width: 1440px; */
                padding: 0 15%;
                position: fixed;
                width: 100%;
                top: 0;
                left: 0;

                z-index: 10; // top of page content

                align-items: center; // for hamburger

                box-sizing: border-box;
                background-color: #20232a;
                color: #ffffff;
                /* background-color: #ececec;
                color: #000000; */
                @media (max-width: 1200px) {
                    padding: 0 5%;
                }
                @media (max-width: 768px) {
                    padding: 0 3%;
                    position: fixed;
                    top: 0;
                    left: 0;
                    z-index: 10;
                    /* border: 1px solid red; */
                    width: 100%;
                }
                @media (max-width: 576px) {
                    padding: 0 5%;
                }
                /* border: 1px solid red; */
            `}
        >
            <HeaderHambuger menuHandler={() => setShow((p) => !p)} />
            <div
                css={css`
                    display: none;
                    @media (max-width: 576px) {
                        display: block;
                        position: absolute;
                        left: 50%;
                        top: 16px;

                        transform: translate(-50%);
                    }
                `}
            >
                <Link to="/" onClick={() => setShow(false)}>
                    <img
                        style={{ width: '30px', height: '30px' }}
                        src="data:image/svg+xml;base64,"
                        alt=""
                        height="20"
                    ></img>
                </Link>
            </div>

            {/* 
            <StyledMenu className={show ? classes.a : ''}>
                <HeaderLink menuHandler={() => setShow((p) => !p)}/>
                <HeaderMemberLink menuHandler={() => setShow((p) => !p)}/>
            </StyledMenu> */}

            {/*  */}
            <div className={`${show ? classes.a : ''} ${classes.b}`}>
                <HeaderLink />
                <HeaderMemberLink />
            </div>
            {/*  */}
            {/* <button onClick={() => setShow((p) => {
                console.log(p)
                return !p
            })}>CLick</button> */}
        </div>
    )
}

export default TheHeader

