/** @jsxImportSource @emotion/react */

import { css } from '@emotion/react'
// import classes from './HeaderLink.module.scss'

import { NavLink } from 'react-router-dom'

// mui
import HomeIcon from '@mui/icons-material/Home'

function HeaderLink(props) {
    return (
        <ul
            css={css`
                display: flex;
                @media (max-width: 576px) {
                    flex-direction: column;
                    align-items: center;
                }
                & li {
                    margin: 0 10px;
                    @media (max-width: 768px) {
                        margin: 0 6px;
                    }
                    @media (max-width: 576px) {
                        margin: 5px 0;
                    }
                    a {
                        display: flex;
                        height: 100%;
                        align-items: center;
                        /* padding: 0 5px; */
                        /* align-content: center; */
                        &:link {
                            color: inherit;
                        }
                        &:visited {
                            color: inherit;
                        }
                        &:hover {
                            color: #61dafb;
                        }
                    }
                }
            `}
        >
            <li
                css={css`
                    @media (max-width: 768px) {
                        display: none;
                    }
                `}
            >
                <NavLink to="/">
                    <div
                        css={css`
                            display: flex;
                            align-items: center;
                        `}
                    >
                        <HomeIcon />
                        回首頁
                    </div>
                </NavLink>
            </li>
            <li
                css={css`
                    display: none;
                    @media (max-width: 768px) {
                        display: block;
                    }
                    @media (max-width: 576px) {
                        display: none;
                    }
                `}
            >
                <NavLink to="/">
                    <img
                        src="data:image/svg+xml;base64,"
                        alt=""
                        height="20"
                    ></img>
                </NavLink>
            </li>
            <li onClick={props.menuHandler}>
                <NavLink to="/download-app" onClick={props.menuHandler}>
                    下載APP
                </NavLink>
            </li>
        </ul>
    )
}

export default HeaderLink
