/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react'
import { NavLink } from 'react-router-dom'

function HeaderMemberLink(props) {
    return (
        <nav
            css={css`
                height: 100%;
            `}
        >
            <ul
                css={css`
                    display: flex;
                    align-items: center;
                    height: 100%;
                    @media (max-width: 576px) {
                        flex-direction: column;
                    }
                    span {
                        @media (max-width: 576px) {
                            display: none;
                        }
                    }
                    & li {
                        margin: 0 10px;
                        @media (max-width: 768px) {
                            margin: 0 6px;
                        }
                        @media (max-width: 576px) {
                            margin: 15px 0;
                        }
                        a {
                            display: flex;
                            height: 100%;
                            align-items: center;
                            /* padding: 0 5px; */
                            /* align-content: center; */
                            &:link {
                                color: inherit;
                            }
                            &:visited {
                                color: inherit;
                                &:hover {
                                    color: #61dafb;
                                }
                            }
                        }
                    }
                `}
            >
                <li>
                    <NavLink to="/login" onClick={props.menuHandler}>
                        登入
                    </NavLink>
                </li>
                <span>|</span>
                <li>
                    <NavLink to="/register" onClick={props.menuHandler}>
                        註冊
                    </NavLink>
                </li>
                <span>|</span>
                <li>
                    <NavLink to="/order" onClick={props.menuHandler}>
                        會員中心
                    </NavLink>
                </li>
                <span>|</span>
                <li>
                    <NavLink to="/member" onClick={props.menuHandler}>
                        查訂單
                    </NavLink>
                </li>
                <span>|</span>
                <li>
                    <NavLink to="/cart" onClick={props.menuHandler}>
                        購物車
                    </NavLink>
                </li>
                <span>|</span>
                <li>
                    <NavLink to="/check" onClick={props.menuHandler}>
                        結帳
                    </NavLink>
                </li>
            </ul>
        </nav>
    )
}

export default HeaderMemberLink
