/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react'

const styleForm = css`
    display: flex;

    /* center form */
    flex: 1 1 auto;
    justify-content: center;
`

const styleInput = css`
    background-color: #404246;
    border-radius: 0.25rem;
    border: 2px solid #383c44;
    border-radius: 15px 0px 0px 15px;
    padding: 5px 9px;
    font-size: 20px;
    color: white;
    /* box-shadow: none; */
    &:focus {
        outline: none;
    }
    @media (max-width: 1200px) {
        width: 180px;
    }
`

const styleButton = css`
    border-radius: 0px 15px 15px 0px;
    border: 2px solid #7e8696;
    background-color: #7e8696;
    padding: 5px 20px;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
`

function TheSearch() {
    return (
        <form css={styleForm}>
            <input type="text" placeholder="鍋物" css={styleInput} />
            <button css={styleButton}>搜尋</button>
        </form>
    )
}

export default TheSearch
