/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react'

const styleLogo = css`
    color: #61dafb;
    font-family: Segoe UI;
    font-weight: 600;
    font-size: 50px;
`

function TheLogo() {
    return (
        <div
            css={css`
                display: flex;
                @media (max-width: 768px) {
                    margin-bottom: 16px;
                }
            `}
        >
            <div>
                <h1 css={styleLogo}>React</h1>
            </div>
            <div
                css={css`
                    display: flex;
                    align-items: flex-end;
                    padding-bottom: 8px;
                    padding-left: 18px;
                    font-size: 22px;
                `}
            >
                <p
                    css={css`
                        color: white;
                        font-weight: 500;
                        font-family: Oxygen, Ubuntu, Cantarell, Fira Sans,
                            Droid Sans;
                    `}
                >
                    購物網
                </p>
            </div>
        </div>
    )
}

export default TheLogo
