/** @jsxImportSource @emotion/react */
import classes from './App.module.scss'
import { css } from '@emotion/react'

import { useState, useEffect } from 'react'
import { Transition } from 'react-transition-group'
import { Routes, Route } from 'react-router-dom'

// Layout
import TheHeader from './components/LayoutHeader/TheHeader.jsx'
import TheHeadContent from './components/LayoutHeadContent/TheHeadContent.jsx'

// pages
import Home from './components/Pages/Home.jsx'
import Member from './components/Pages/Member.jsx'
import DownloadApp from './components/Pages/DownloadApp.jsx'
import { getAccordionDetailsUtilityClass } from '@mui/material'

function App() {
    const [show, setShow] = useState(false)
    const transitionStyles = {
        entering: {
            opacity: 1,
            visibility: 'visible',
            transition: `visibility 0s linear, opacity 2s ease-in-out`,
        },
        entered: { opacity: 1, visibility: 'visible' },
        exiting: {
            opacity: 0,
            visibility: 'hidden',
            transition: `visibility 0s linear 2s, opacity 2s ease-in-out`,
        },
        exited: { opacity: 0, visibility: 'hidden' },
    }

    async function getData() {
        // const response = await fetch('https://jsonplaceholder.typicode.com/todos/1')
        const response = await fetch('./json/test.json')
        const data = await response.json()
        console.log(data)
    }

    useEffect(() => {
        getData()
    }, [])
    return (
        <div>
            {/* TheHeader */}
            <TheHeader />
            {/* TheHeader */}
            <TheHeadContent />
            {/* HeadContent */}

            {/* HeadContent */}

            {/* Routes */}
            <div
                css={css`
                    @media (max-width: 576px) {
                        margin-top: 40px;
                    }
                `}
            >
                <Routes>
                    <Route path="/" element={<Home />}></Route>
                    <Route path="/member" element={<Member />}></Route>
                    <Route
                        path="/download-app"
                        element={<DownloadApp />}
                    ></Route>
                </Routes>
            </div>
            {/* Routes */}

            <div style={{ height: '300px' }}>HEight</div>
            <Transition in={show} timeout={3000}>
                {(state) => (
                    <div
                        style={{
                            opacity: 0,
                            cursor: 'pointer',
                            ...transitionStyles[state],
                        }}
                    >
                        Fade
                    </div>
                )}
            </Transition>
            <button onClick={() => setShow((p) => !p)}>CLick</button>
            <p className={classes.a}>1234</p>
            <p className={classes.b}>1234</p>
            <p className={classes.c}>1234</p>
            <p className={classes.d}>1234</p>
            <p className={classes.a}>1234</p>
            <p className={classes.b}>1234</p>
            <p className={classes.c}>1234</p>
            <p className={classes.d}>1234</p>
            <p className={classes.a}>1234</p>
            <p className={classes.b}>1234</p>
            <p className={classes.c}>1234</p>
            <p className={classes.d}>1234</p>
            <p className={classes.a}>1234</p>
            <p className={classes.b}>1234</p>
            <p className={classes.c}>1234</p>
            <p className={classes.d}>1234</p>
        </div>
    )
}

export default App
